:root{
    --primary:#fff;
}

.btn{
    padding:8px 20px;
    border-radius:4px;
    outline:none;
    border:none;
    cursor:pointer;
    white-space: nowrap;
    margin:5px;
}

.btn--primary{
    background-color: var(--primary);
    color:#242424;
    border:1px solid var(--primary);
}

.btn--outline{
    background-color: transparent;
    color:#fff;
    padding: 8px 20px;
    border:1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium{
    padding: 8px 20px;
    font-size: 18px;
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--mobile{
    text-align: center;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 24px;
    background-color: transparent;
    color:#fff;
    padding:14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
}

.btn--wide{
    padding:12px 64px;
    font-size:20px;
}

.btn--large:hover, .btn--mobile:hover, .btn--medium:hover{
    transition: all 0.3s ease-out;
    background-color:#fff;
    color:#242424;
}

.btn--wide:hover{
    background-color: #f00946;
    color: #fff;
    transition: all 0.2s ease-out;
}

.blue{
    background-color: #276afb;
    color:#fff;
    border: none;
}

.red{
    background-color: #f00946;
    color:#fff;
    border: none;
}

.primary{
    background-color: #fff;
    color:#242424;
    border: none;
}

.primary:hover{
    background-color: #242424;
    color:#fff;
}

.green{
    background-color: #25ce4a;
    color:#fff;
    border: none;
}
.green:hover{
    background-color: #242424;
}

.purple{
    background-color: #800080;
    color:#fff;
    border: none;
}

.btn-link{
    display:flex;
    justify-content: center;
    align-items: center;
    text-decoration:none;
    padding: 8px 16px;
    height: 100%;
    width: 100%;
}